.popup-menu {
  position: fixed;
  z-index: 100;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  transition: opacity 200ms;
  visibility: hidden;
  opacity: 0;
}

.popup-menu .header-top_menu {
  display: block;
  background-color: #ae2020;
  height: 100%;
  width: 50%;
  transition: transform ease 0.3s;
  transform: translate3d(-100%, 0, 0);
}

@media (max-width: 576px) {
  .popup-menu .header-top_menu {
    width: 100%;
  }
}

.popup-menu .header-top_menu .menu_item {
  display: block;
}

.popup-menu .header-top_menu .menu_item .menu_item__link {
  padding: 18px 20px;
  font-size: 37px;
}

@media (max-width: 576px) {
  .popup-menu .header-top_menu .menu_item .menu_item__link {
    font-size: 28px;
  }
}

.popup-menu .header-top_menu .menu_item .menu_item__link:hover {
  top: -4px;
}

.popup-menu.popup-menu__toggle {
  visibility: visible;
  opacity: 1;
}

.popup-menu.popup-menu__toggle .header-top_menu {
  transform: translate3d(200%, 0, 0);
}

@media (max-width: 576px) {
  .popup-menu.popup-menu__toggle .header-top_menu {
    transform: translate3d(100%, 0, 0);
  }
}
